.searchList .musicList ul {
  display: flex;
}

.searchList .musicList ul li {
  color: #999;
  font-size: 12px;
  padding: 0 5px;
}

.searchList .musicList .musicList_title {
  height: 30px;
}

.searchList .musicList .musicList_title li {
  line-height: 30px;
}

.searchList .musicList .musicList_content a {
  display: block;
  height: 30px;
}

.searchList .musicList .musicList_content a li {
  line-height: 30px;
}

.searchList .musicList .musicList_content a li .mv {
  display: inline-block;
  color: #000;
  height: 20px;
  width: 20px;
  line-height: 20px;
  border: 1px solid;
  margin-right: 8px;
}

.searchList .musicList .num {
  width: 10%;
}

.searchList .musicList .name {
  width: 25%;
}

.searchList .musicList .artist {
  width: 22%;
}

.searchList .musicList .album {
  width: 31%;
}

.searchList .musicList .time {
  width: 12%;
}

.searchList .fenYe {
  margin-top: 20px;
}
