.search .input {
  background: url("http://h5static.kuwo.cn/www/kw-www/img/07b12b9.png") no-repeat;
  background-size: 100% 100%;
  height: 120px;
  position: relative;
}

.search .input .content-inp {
  height: 24px;
  width: 256px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.search .input .content-inp input {
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  box-sizing: border-box;
  padding-left: 14px;
}

.search .input .content-inp .hearSearch {
  width: 100%;
  position: absolute;
  left: 0;
  top: 30px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 0 8px #f0f0f0;
  box-sizing: border-box;
  padding: 10px;
}

.search .input .content-inp .hearSearch .title {
  font-weight: 700;
  line-height: 30px;
  font-size: 18px;
}

.search .input .content-inp .hearSearch ul {
  font-size: 16px;
}

.search .input .content-inp .hearSearch ul li {
  line-height: 26px;
}

.search .input .content-inp .hearSearch ul li a {
  display: block;
  height: 100%;
  color: #999;
}

.search .searchJieGuo {
  padding-left: 20px;
  padding-top: 20px;
}

.search .searchJieGuo .title {
  font-weight: 700;
  line-height: 30px;
  font-size: 18px;
  display: inline-block;
  padding-right: 20px;
}

.search .searchJieGuo ul {
  font-size: 14px;
  height: 26px;
  display: inline-block;
  vertical-align: bottom;
}

.search .searchJieGuo ul li {
  line-height: 26px;
  float: left;
  margin-right: 15px;
  color: #999;
}

.search .searchJieGuo .active {
  font-weight: 700;
  color: #e40be4 !important;
}

.search .route {
  margin-top: 20px;
}
