.musicPlay {
  padding-top: 20px;
  color: #999;
}

.musicPlay .xinXi {
  line-height: 30px;
  text-align: center;
}

.musicPlay .xinXi .name {
  font-size: 20px;
  color: #000;
  font-weight: 700;
  margin-right: 10px;
}

.musicPlay .xinXi .artist {
  font-size: 14px;
  margin-left: 10px;
}

.musicPlay .img {
  width: 200px;
  height: 200px;
  margin: 0 auto;
  margin-top: 30px;
  border-radius: 50%;
  overflow: hidden;
}

.musicPlay .img img {
  width: 100%;
  height: 100%;
}

.musicPlay .zhuanJi {
  padding-top: 20px;
  text-align: center;
  height: 30px;
  font-size: 0;
}

.musicPlay .zhuanJi span {
  font-size: 14px;
  display: inline-block;
  line-height: 30px;
  height: 30px;
  vertical-align: top;
}

.musicPlay .zhuanJi .tip {
  max-width: 100px;
  margin-right: 20px;
}

.musicPlay .zhuanJi .zhuanJi_time {
  max-width: 100px;
}

.musicPlay .lrc {
  margin-top: 20px;
  height: 210px;
  overflow: hidden;
}

.musicPlay .lrc .lrcContent {
  transition: all 0.8s linear;
}

.musicPlay .lrc .lrcContent div {
  text-align: center;
  line-height: 30px;
  font-size: 14px;
}

.musicPlay .lrc .lrcContent div.active {
  font-weight: 700;
  color: #000;
}

.musicPlay .time {
  margin-top: 20px;
  padding: 0 10px;
  height: 36px;
}

.musicPlay .time .timeCon {
  height: 100%;
  display: flex;
  align-items: center;
}

.musicPlay .time .timeCon .zong,
.musicPlay .time .timeCon .dang {
  width: 70px;
  box-sizing: border-box;
  line-height: 36px;
  text-align: center;
  font-size: 14px;
  color: #999;
}

.musicPlay .time .jin {
  height: 3px;
  background: #999;
  position: relative;
  flex: 1 0 auto;
}

.musicPlay .time .jin .t {
  height: 100%;
  width: 30px;
  background: #6824b6;
}

.musicPlay .time .jin .dian {
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  top: -2px;
  left: 30px;
  transform: translateX(-50%);
  background: #1ddf26;
}

.musicPlay .caiDan {
  position: fixed;
  width: 100%;
  bottom: 0;
  height: 50px;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  color: #fff;
}

.musicPlay .caiDan i {
  padding: 0 20px;
}
