.fenYe_content {
  text-align: center;
  font-size: 0;
}

.fenYe_content span {
  display: inline-block;
  line-height: 40px;
  width: 40px;
  text-align: center;
  font-size: 14px;
  background: #444;
  border-radius: 50%;
  color: #fff;
  margin: 0 14px;
  cursor: pointer;
}

.fenYe_content span.active {
  background: #f60291;
}
