.mvPlay {
  height: 210px;
  background: #333;
  transition: all 0.6s linear;
}

.mvPlay.quanping {
  width: 100vh;
  height: 100vw;
  transform: rotate(90deg) translateY(-100vw);
  transform-origin: 0 0;
}

.mvPlay .video {
  height: 100%;
}

.mvPlay .video .v {
  height: 100%;
  background: #000;
  position: relative;
}

.mvPlay .video .v video {
  width: 100%;
  height: 100%;
  display: block;
}

.mvPlay .video .v .menu {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 36px;
  padding-left: 10px;
  box-sizing: border-box;
  background-color: rgba(43, 51, 63, 0.7);
  display: flex;
}

.mvPlay .video .v .menu .chongXinPlay {
  height: 36px;
  line-height: 36px;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin-right: 10px;
}

.mvPlay .video .v .menu .col_c_p {
  height: 36px;
  line-height: 36px;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
}

.mvPlay .video .v .menu .jinDu {
  padding: 0 10px;
  flex: 1;
  height: 36px;
  display: flex;
  align-items: center;
}

.mvPlay .video .v .menu .jinDu .t {
  width: 70px;
  height: 36px;
  line-height: 36px;
  font-size: 12px;
  color: #fff;
  margin-left: 6px;
}

.mvPlay .video .v .menu .jinDu .jin {
  flex: 1;
  height: 3px;
  background: #f0f0f0;
  position: relative;
}

.mvPlay .video .v .menu .jinDu .jin .time {
  height: 100%;
  width: 30px;
  background: #6824b6;
}

.mvPlay .video .v .menu .jinDu .jin .dian {
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  top: -2px;
  left: 30px;
  transform: translateX(-50%);
  background: #1ddf26;
}

.mvPlay .video .v .menu .quanPing {
  height: 36px;
  line-height: 36px;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin-right: 20px;
}

.mvPlay .video .v .kuaiJinTiShi {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 20px;
  text-align: center;
  z-index: 1000;
  display: none;
}
